import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import logo from '../assets/images/logo.png';
class BackgroundComponent extends Component {
    render() {
        return (
            <header className="row" id="header">
                <div className="top-banner row m0 text-center fadeInOnLoad">
                    <div className="container">
                        <h2>Connect Everyone &amp; Everything</h2>
                        <p className="box-headline letters rotate-2" style={{ marginTop: '100px' }}>
                            NOW INTEGRATE
              <span className="box-words-wrapper">
                                <b className="is-visible">ACCOUNTING.</b>
                                <b>PAYMENT.</b>
                                <b>COMMUNICATION.</b>
                                <b>SUPPORT.</b>
                                <b>ANALYTICS.</b>
                            </span>
                            with just a click
                    </p>
                        <h3 style={{ marginBottom: '100px' }}></h3>
                        <Link to="#" className="btn btn-primary btn-lg video">Get Started</Link>
                    </div>
                </div>
            </header>
        );
    }
}

export default BackgroundComponent;


