import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import right_angle from '../assets/images/right-angle-white.png';
import protect from '../assets/images/protect.png';

class ContactComponent extends Component {
    render() {
        return (
            <section className="row newsletter" id="contact">
                <div className="container">
                    <div className="row section-header wow fadeInUp">
                        <h2>Contact Us</h2>
                        <p>Subscribe to our product newsletter to get notified when we launch the product or when we completed a milestone</p>
                    </div>
                    <form action="" id="subscribeform" className="row newsletter-form" method="post">
                        <div className="input-group">
                            <input type="email" className="form-control" name="email" placeholder="Enter Your Email Address" />
                            <span className="input-group-addon">
                                <button type="submit" id="js-subscribe-btn">
                                    <img src={right_angle} alt="" />
                                </button>
                            </span>
                        </div>
                        <div id="js-subscribe-result" className="text-center" data-success-msg="Almost finished. Please check your email and verify."
                            data-error-msg="Oops. Something went wrong.">
                            <p>
                                <img src={protect} alt="" />No Spam. We Promise. Unsubscribe anytime.
                        </p>
                        </div>
                    </form>
                </div>
            </section>
        );
    }
}

export default ContactComponent;


