import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

class WorkComponent extends Component {

    render() {
        let list = this.props.data.list.map((item) => {
            return <div key={item.key} className="col-sm-4 work-process wow fadeIn">
                <div className="row m0 process-icon">
                    <img src={item.icon} alt="" />
                </div>
                <h3>{item.title}</h3>
                <p>{item.detail}</p>
            </div>
        });
        return (
            <section className="row how-it-works" id="process">
                <div className="container">
                    <div className="row section-header v2 wow fadeInUp">
                        <h2>{this.props.data.heading}</h2>
                        <p>{this.props.data.desc}</p>
                    </div>
                    <div className="row work-processes">
                        {list}
                    </div>
                </div>
            </section>
        );
    }
}

export default WorkComponent;


