import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import '../assets/css/plugins/lineicons.css';
class FeatureComponent extends Component {

    render() {
        let list = this.props.data.list.map((item) => {
            return <div key={item.key} className="col-sm-6 col-md-4 benefit wow fadeInUp">
                <Link to="page-2" >
                    <div className="media">
                        <div className="media-left">
                            <span>
                                <i className={item.icon}></i>
                            </span>
                        </div>
                        <div className="media-body">
                            <h4>{item.title}</h4>
                            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
                labore et dolore </p>
                        </div>
                    </div>
                </Link>
            </div>
        });
        return (
            <section className="row the-benefits" id="features" style={{ borderBottom: '1px solid #ccc' }}>
                <div className="container">
                    <div className="row section-header wow fadeInUp">
                        <h2>{this.props.data.heading}</h2>
                        <p>{this.props.data.desc}</p>
                    </div>
                    <div className="row benefit-notes">
                        {list}
                    </div>
                </div>
            </section>
        );
    }
}

export default FeatureComponent;


