import React from 'react'
import Link from 'gatsby-link'

import BackgrounComponent from '../layouts/background';
import WorkComponent from '../layouts/work';
import FeatureComponent from '../layouts/features';
import ContactComponent from '../layouts/contact';
import FooterComponent from '../layouts/footer';

import desktop from '../assets/images/desktop.png';
import toggles from '../assets/images/toggles.png';
import trophy from '../assets/images/trophy.png';

let work = {
  heading: 'how it works',
  desc: 'DuBase help you build better apps to grow your business in just simple steps.',
  list: [
    { key: '0', detail: 'detail', icon: desktop, title: 'Install' },
    { key: '1', detail: 'detail', icon: toggles, title: 'Configure it' },
    { key: '2', detail: 'detail', icon: trophy, title: 'Yay! Done.' },
  ]
};
let features = {
  heading: 'THE BENEFITS',
  desc: 'List out your product’s benefit here. A small description about what it is and how it helps the user. You can also add some icons.',
  list: [
    { key: '0', detail: 'detail', icon: 'ti-file', title: 'Accounting' },
    { key: '1', detail: 'detail', icon: 'ti-money', title: 'Payment' },
    { key: '2', detail: 'detail', icon: 'ti-comments', title: 'Communication' },
    { key: '3', detail: 'detail', icon: 'ti-bar-chart', title: 'Analytics' },
    { key: '4', detail: 'detail', icon: 'ti-lock', title: 'Security' },
    { key: '5', detail: 'detail', icon: 'ti-timer', title: '24/7 Support' },
  ]
};

const IndexPage = () => (
  <div>
    <BackgrounComponent />
    <WorkComponent data={work} />
    <FeatureComponent data={features} />
    <ContactComponent />
    <FooterComponent />
  </div >
)

export default IndexPage



